import {getPlayerDetail} from  "../../api/player.js"
import {
	getFilterItem
} from "../../utils/constant.js"
export default {
	data() {
		return {
			player: {}
		}
	},
	async onLoad({ id }) {
		const { code, data } = await getPlayerDetail({id})
		if (code > 0) {
			const { icon: roleIcon, label: roleName } = getFilterItem('hero.role', data.role);
			data.roleIcon = roleIcon
			data.roleName = roleName
			this.player = data;
		}
	},
	methods: {
		
	}
}
